<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }"> Каталог </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }"> Корзина </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> {{products.length}} товара </span>
    </div>

    <div v-if="cartLoading" class="preloader_preload"></div>
    <section class="cart">
      <form class="cart__form form" action="#" method="POST"
        @submit.prevent="createOrder">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText title="ФИО"
              placeholder="Введите ваше полное имя"
              type="name"
              :error="formError.name"
              v-model="formData.name" />
            <BaseFormText title="Адрес доставки"
              placeholder="Введите ваш адрес"
              :error="formError.address"
              v-model="formData.address" />
            <BaseFormText title="Телефон"
              placeholder="Введите ваш телефон"
              type="tel"
              :error="formError.phone"
              v-model="formData.phone" />
            <BaseFormText title="Email"
              placeholder="Введите ваш Email"
              type="email"
              :error="formError.email"
              v-model="formData.email" />
            <BaseFormTextarea
              title="Комментарий к заказу"
              placeholder="Ваши пожелания"
              v-model="formData.comment"
              :error="formError.comment"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0"
                    checked=""
                  />
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="500"
                  />
                  <span class="options__value"> Курьером <b>500 ₽</b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="card"
                  />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="cash"
                  />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="p in products" :key="p.id">
              <h3>{{p.product.title}}</h3>
              <b>{{p.product.price | numberFormat}} ₽</b>
              <span>Артикул: {{p.product.id}}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{products.length}}</b> товара на сумму
            <b>{{totalPrice | numberFormat}} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMsg">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{formErrorMsg}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters } from 'vuex';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { API_BASE_URL } from '../config';

export default {
  components: { BaseFormText, BaseFormTextarea },
  filters: { numberFormat },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMsg: '',
      cartLoading: false,
    };
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
  methods: {
    createOrder() {
      this.formError = {};
      this.formErrorMsg = '';
      this.cartLoading = true;

      axios.post(`${API_BASE_URL}/api/orders`, {
        ...this.formData,
      }, {
        params: { userAccessKey: this.$store.state.userAccessKey },
      })
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMsg = error.response.data.error.message;
        })
        .then(() => {
          this.cartLoading = false;
        });
    },
  },
};
</script>

<style scoped>
.preloader_preload {
  display: block;
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-width: 1000px;
  background: rgba(0, 0, 0, 0.3) url(../../public/img/svg/rings.svg)
    center center no-repeat;
  background-size: 69px;
}
</style>
