<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Корзина </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> {{products.length}} товара </span>
    </div>

  <div v-if="cartLoading" class="hellopreloader_preload"></div>
  <div
    class="load-info page-size"
    v-if="cartLoading">
    Загрузка корзины...
  </div>
  <div
    class="load-info page-size"
    v-else-if="isCartEmpty">
    Корзина пуста
  </div>
  <div
    class="load-info page-size"
    v-else-if="cartLoadingFailed">
    Произошла ошибка при загрузке корзины
  </div>
    <section class="cart" v-else>
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem  v-for="item in products" :key="item.productId" :item="item" />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">Итого: <span>{{totalPrice | numberFormat}} ₽</span></p>

          <router-link
            tag="button"
            :to="{name: 'order'}"
            class="cart__button button button--primery"
            type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import CartItem from '@/components/CartItem.vue';
import numberFormat from '@/helpers/numberFormat';

export default {
  components: { CartItem },
  filters: { numberFormat },
  data() {
    return {
      isCartEmpty: false,
    };
  },
  computed: {
    ...mapState(['cartLoadingFailed', 'cartLoading']),
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
  watch: {
    products() {
      this.isCartEmpty = !this.products.length;
    },
  },
  created() {
    this.isCartEmpty = this.cartLoading ? false : !this.products.length;
    console.log('cartDetailProducts', this.products);
  },
};
</script>

<style scoped>
.load-info {
  font-size: 20px;
  color: gray;
  text-align: center;
}
.hellopreloader_preload {
  display: block;
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  width: 100%;
  height: 80%;
  min-width: 1000px;
  background: url(../../public/img/svg/rings.svg)
    center center no-repeat;
  background-size: 69px;
}
.page-size {
  height: 35vh;
}
</style>
