<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }"> Каталог </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }"> Корзина </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 v-if="!cartLoading" class="content__title">
        Заказ оформлен <span>№ {{orderInfo.id}}</span>
      </h1>
    </div>
    <div v-if="cartLoading" class="container_preloader">
      <div class="preloader_preload"></div>
    </div>

    <section class="cart" v-if="!cartLoading">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет
            письмо с&nbsp;деталями заказа. Наши менеджеры свяжутся с&nbsp;Вами
            в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key"> Получатель </span>
              <span class="dictionary__value">
                {{orderInfo.name}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Адрес доставки </span>
              <span class="dictionary__value">
                {{orderInfo.address}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Телефон </span>
              <span class="dictionary__value"> {{orderInfo.phone}} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Email </span>
              <span class="dictionary__value"> {{orderInfo.email}} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Способ оплаты </span>
              <span class="dictionary__value"> картой при получении </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="p in orderInfo.basket.items" :key="p.id">
              <h3>{{p.product.title}}</h3>
              <b>{{p.product.price | numberFormat}} ₽</b>
              <span>Артикул: {{p.product.id}}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{orderInfo.basket.items.length}}</b> товара на сумму
            <b>{{orderInfo.totalPrice | numberFormat}} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapState } from 'vuex';
import numberFormat from '@/helpers/numberFormat';

export default {
  filters: { numberFormat },
  data() {
    return {
      cartLoading: true,
    };
  },
  watch: {
    orderInfo() {
      this.cartLoading = false;
    },
    '$route.params.id': {
      handler() {
        if (!!this.orderInfo && this.orderInfo.id === this.$route.params.id) {
          this.cartLoading = false;
          return;
        }
        this.$store.dispatch('loadOrderInfo', this.$route.params.id);
      },
      immediate: true,
    },
  },
  computed: {
    ...mapState(['orderInfo']),
  },
};
</script>

<style scoped>
.container_preloader {
  height: 50vh;
}
.preloader_preload {
  display: block;
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-width: 1000px;
  background: rgba(0, 0, 0, 0.2) url(../../public/img/svg/rings.svg)
    center center no-repeat;
  background-size: 69px;
}
</style>
