<template>
  <main class="content container">
    <div v-if="productsLoading" class="container_preloader">
      <div class="preloader_preload"></div>
    </div>
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> 152 товара </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :priceFrom.sync="filterPriceForm"
        :priceTo.sync="filterPriceTo"
        :categoryId.sync="filterCategoryId"
        :colorId.sync="filterColorId"
      />

      <section class="catalog">
        <div class="load-info" v-if="productsNotFound">Товаров нет</div>
        <div class="load-info" v-if="productsLoading">Загрузка товаров...</div>
        <div class="load-info" v-if="productsLoadingFailed">
          Произошла ошибка при загрузке товаров
          <button @click.prevent="loadProducts">Попробовать еще раз</button>
        </div>
        <ProductList :products="products" />
        <BasePagination
          :page.sync="page"
          :count="countProducts"
          :perPage="productsPerPage"
        />
      </section>
    </div>
  </main>
</template>

<script>
import axios from 'axios';
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import { API_BASE_URL } from '../config';

export default {
  name: 'MainPage',
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceForm: 0,
      filterPriceTo: 35000,
      filterCategoryId: 0,
      filterColorId: 0,
      page: 1,
      productsPerPage: 6,
      productsData: null,
      loadProductsTimer: 0,
      productsNotFound: false,
      productsLoading: true,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items.map((product) => ({
        ...product,
        img: product.image.file.url,
      })) : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              minPrice: this.filterPriceForm,
              maxPrice: this.filterPriceTo,
              categoryId: this.filterCategoryId,
              colorId: this.filterColorId,
            },
          })
          .then((response) => {
            this.productsData = response.data;
            this.productsNotFound = !response.data.items.length;
            console.log('response.data.items.length', !response.data.items.length);
            console.log('response.data.items', response.data.items);
          })
          .catch((error) => {
            console.error('error', error);
            this.productsLoadingFailed = true;
          })
          .then(() => {
            this.productsLoading = false;
          });
      }, 0);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceForm() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>

