.load-info {
  color: gray;
  text-align: center;
  font-size: 18px;
}

.container_preloader {
  display: block;
  position: absolute;
  z-index: 99;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
}
.preloader_preload {
  display: block;
  position: fixed;
  z-index: 99;
  top: 0;
  left: 150px;
  width: 100%;
  height: 100%;
  min-width: 1000px;
  background: url(../../public/img/svg/rings.svg)
    center center no-repeat;
  background-size: 69px;
}
