<template>
  <li class="cart__item product parent" v-if="item.product.title">
    <div v-show="loading" class="hellopreloader_preload"></div>
    <div class="product__pic">
      <img
        :src="item.product.img"
        width="120"
        height="120"
        :alt="item.product.title"
      />
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code"> Артикул: {{ item.product.id }} </span>

    <div class="product__counter form__counter">
      <button
        type="button"
        aria-label="Убрать один товар"
        @click.prevent="changeAmountProduct(amount - 1)"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input
        type="number"
        disabled
        v-model.number="amount"
        name="count"
      />

      <button
        type="button"
        aria-label="Добавить один товар"
        @click.prevent="changeAmountProduct(amount + 1)"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ (item.amount * item.product.price) | numberFormat }} ₽
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click="deleteClick"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';

export default {
  props: ['item'],
  filters: { numberFormat },
  data() {
    return {
      loading: false,
    };
  },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(val) {
        this.loading = true;
        this.updateAmount({
          productId: this.item.productId,
          amount: val,
        })
          .then(() => {
            this.loading = false;
          });
      },
    },
  },
  methods: {
    ...mapActions({ deleteProduct: 'deleteCartProduct', updateAmount: 'updateCartProductAmount' }),
    changeAmountProduct(productAmount) {
      if (productAmount) {
        this.amount = productAmount;
      }
    },
    deleteClick() {
      this.loading = true;
      this.deleteProduct(this.item.productId)
        .then(() => {
          this.loading = false;
        });
    },
  },
};
</script>

<style scoped>
.parent {
  position: relative;
}
.hellopreloader_preload {
  display: block;
  position: absolute;
  z-index: 99;
  border-radius: 10px;
  top: -20px;
  left: -20px;
  width: 105%;
  height: 120%;
  min-width: 100px;
  background: rgba(0,0,0,0.2) url(../../public/img/svg/rings.svg)
    center center no-repeat;
  background-size: 69px;
}
</style>
