import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '../config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    cartLoading: true,
    cartLoadingFailed: false,
    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find((a) => a.productId === productId);
      if (item && amount) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((a) => a.productId !== productId);
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, productsData) {
      state.cartProductsData = productsData;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
      }));
    },
    changeFlagCartLoadingFailed(state, val) {
      state.cartLoadingFailed = val;
    },
    changeFlagCartLoading(state, val) {
      state.cartLoading = val;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios.get(`${API_BASE_URL}/api/orders/${orderId}`, {
        params: { userAccessKey: context.state.userAccessKey },
      })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
    loadCart(context) {
      return axios.get(`${API_BASE_URL}/api/baskets`, {
        params: { userAccessKey: context.state.userAccessKey },
      })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        })
        .catch((e) => {
          console.error('error', e);
          context.commit('changeFlagCartLoad', true);
        })
        .then(() => context.commit('changeFlagCartLoading', false));
    },
    addProductToCart(context, payload) {
      return axios.post(`${API_BASE_URL}/api/baskets/products`,
        { productId: payload.productId, quantity: payload.amount },
        {
          params: { userAccessKey: context.state.userAccessKey },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          console.log('addProductToCart', response);
          context.commit('syncCartProducts');
        })
        .catch((e) => {
          console.error('error', e);
          context.commit('changeFlagCartLoad', true);
        });
    },
    updateCartProductAmount(context, payload) {
      context.commit('updateCartProductAmount', payload);
      if (payload.amount < 1) {
        return 0;
      }
      return axios.put(`${API_BASE_URL}/api/baskets/products`,
        { productId: payload.productId, quantity: payload.amount },
        {
          params: { userAccessKey: context.state.userAccessKey },
        })
        .then((response) => {
          console.log('updateCartProductsData', response);
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    deleteCartProduct(context, productId) {
      return axios.delete(`${API_BASE_URL}/api/baskets/products`,
        {
          params: { userAccessKey: context.state.userAccessKey },
          data: { productId },
        })
        .then((response) => {
          console.log('response', response);
          console.log('deleteCartProduct', productId);
          context.commit('deleteCartProduct', productId);
        });
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const product = state.cartProductsData.find((p) => p.product.id === item.productId);
        return {
          ...item,
          product: {
            ...product.product,
            img: product.product.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => (
        item.product.price * item.amount) + acc, 0);
    },
  },
});
